*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar{
  font-family: 'Montserrat', sans-serif;
  font-size: 17px;
 
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow:0px 8px 6px -6px gray;
}
.nav-link{
  font-weight: 500 !important;
}
.active{
  border-bottom: 1px solid green;
}
.navbar-brand{
  font-weight: 700;
  font-size: 25px;
}


