.login{
  min-height: 65vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loginWrapper{
  width: 70%;
  height: 70%;
  display: flex;
}
.loginLeft,.loginRight{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.loginLogo{
  font-size: 50px;
  font-weight: 800;
  color: black;
  margin-bottom: 10px;
}
.loginDesc{
     font-size: 24px;
}
.loginBox{
  height: 300px;
  width: 300px;
  padding: 15px;
  background-color: white;
  border: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.loginInput{
  height: 30px;
  border-radius: 10px;
  border: 1px solid grey;
  font-size: 18px;
  padding-left: 10px;
}
.loginInput:focus{
  outline: none;
}
.loginButton{
  height: 30px;
  border-radius: 10px;
  border: none;
  background-color: black;
  color: white;
  font-size: 10px;
  font-weight: 500;
  cursor: pointer;
}
.loginForgot{
  text-align: center;
  color: #1775ee;
  cursor: pointer;
}
.loginRegisterButton{
  width: 40%;
  align-self: center;
  height: 30px;
  border-radius: 10px;
  border: none;
  background-color: #42b72a;
  color: white;
  font-size:10px;
  font-weight: 500;
  cursor: pointer;
}